.footer {
    height: 200px;
    background-color: cadetblue;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

}


.container {
     width: 80%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border: 1px solid;

    flex-wrap: wrap;


}

.footerHeader {
    display: flex;
    height: 50px;
    width: 100%;
    justify-content: center;
    align-items: center;

}
.linkIcons{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 80%;
    border: 1px solid;
}
.link{
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: space-around;

}
.law {
    display: flex;
    height: 35px;
    width: 100%;
    justify-content: center;
    align-items: center;
}

