.contacts {
    height: 100vh;
    background-color: yellow;
}
.container {
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    border: 1px solid;

}

.contactsHeader {
    display: flex;
    height: 100px;
    width: 100%;
    justify-content: center;
    align-items: center;
}




.form {
   display: flex;
   flex-direction: column;
   margin: 50px auto;
}
.form input {
    height: 30px;
}
.form textarea {
    height: 50px;
}

.form button{
    margin-top: 50px;
    justify-content: flex-end;
}
