.mySkils {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100vh;
    background-color: darkseagreen;
}

.container {

    width: 80%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border: 1px solid;

    flex-wrap: wrap;
}

.skils {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.skilsHeader {
    display: flex;
    height: 100px;
    width: 100%;
    justify-content: center;
    align-items: center;
}


.skilDescription {
    border: 1px dotted;
    height: 250px;
    width: 200px;
    display: inline-block;
    margin: 17px;
}
.skil {
    display: flex;

    justify-content: center

}
.descripton{
    display: flex;
    /*align-items: flex-end;*/
}