.myProjects {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #efd4ff;
    min-height: 100vh;

}

.container {

    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid;

    flex-wrap: wrap;

}

.projectsHeader {

    display: flex;
    height: 100px;
    width: 100%;
    justify-content: center;
    align-items: center;
}



.projects {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    /*margin: 15px 0;*/
    /*display: flex;*/
    /*justify-content: center;*/
    /*align-items: center;*/
    /*border: 1px dotted;*/
    /*flex-wrap: wrap;*/
}
.projects img {
    height: 250px;
    transition: all 0.5s ease;
}


.projects img:hover {

    -webkit-filter: blur(5px);

}

